.App{
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
.Navbar{
  width: 100%;
  height: 74px;
  background-color: black;
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  color: white;
}
.link{
  text-decoration: none;
  color: white;
}
.navleft{
  flex:1;
}
.navright{
  flex:1;
  padding: 10px;
}
.navcenter{
  flex: 10;
}
.navlist{
 list-style: none;
 display: flex;
 justify-content: flex-start;
 margin: 0;
}
.navlist_active{
  display: none;
}
.listitem{
 padding-right: 50px;
 cursor: pointer;
}
.icon{
  padding-right: 10px;
  padding-bottom: 10px;
}
.homePosts{
  display: grid;
  grid-template-columns: 33.33% 33.33% 33.33%;
  grid-template-rows: auto;
  padding: 10px;
  margin: 10px;
  justify-content: center;
  align-items: center;
  justify-items: center;
}
.barToggle{
  display: none;
}
@media screen and (min-width: 400px) and (max-width: 850px) {
  .Navbar{
    width: 100%;
    display: flex;
    position: relative;
    margin: 0;
  }
  .navleft{
    display: none;
  }
  .barToggle{
    display: block;
  }
  .menu-icon{
    width:20px;
    height: 20px;
    padding-left: 10px;
  }
  .navcenter{
    flex: 7;
   }
  .navright{
   flex: 2;
   padding-right: -10px;
  }
  .navlist{
    display: none;
  }
  .navlist.active{
    top: 74px;
    list-style: none;
    position: absolute;
    background-color: black;
    color:white;
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 100%;
    left:0;
    margin: 0;
    padding-left: 0;
  }
  .listitem{
   padding: 10px 0px;
   border-bottom: 0.5px white solid;
   margin: 0;
   width:100%;
   padding-left: 0;
  }
  .homePosts{
    display: grid;
    grid-template-columns: 50%;
    grid-template-rows: auto;
    justify-content: center;
    align-items: center;
    justify-items: center;
  }
}
@media screen and (min-width: 851px)and (max-width: 950px) {
 .navright{
  flex: 2;
 }
 .navcenter{
  flex: 8;
 }
}
@media screen and (min-width: 851px) and (max-width: 1250px){
 .Navbar{
  width: 100%;
 }
 .navcenter{
  flex: 9;
 }
 .navright{
  flex: 2;
 }
 .homePosts{
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: auto;
  justify-content: center;
  align-items: center;
  justify-items: center;
 }
}
.contact{
  margin: 10px;
}
.mapline{
  height:25px;
  text-align: center;
  background-color: black;
  width: 250px;
  margin-left: 40%;
  border-radius: 10px;
  
}
.mapline a{
  text-decoration: none;
  color: white;
}
.mapline a:hover{
  color-scheme: #D4D4D4;
}
.about, .store{
  margin: 10px;
  padding: 10px;
}
.abt, .stt{
  text-align: center;
}
