.post{
    height: 439px;
    border: 1px solid #D4D4D4;
    background-color: #F5F5F5;
    margin: 10px;
    width: 390.55px;
}
.postImg{
    width: 390.55px;
    padding: 0;
    margin: 0;
}
.postBody{
  padding: 0 12px;
}
.postComment{
    color: #6f6f6f;
    font-size: 12px;
    margin-top: 1px;
    margin-bottom: 8px;
}
.postTitle{
  font-weight: 600;
  font-size: 16px;
  line-height: 120%;
  margin-bottom: 12px;
  margin-top: 5px;
}
.postDesc{
    font-size: 14px;
    line-height: 120%;
    margin: 0;
}
.postFoot{
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
    margin-top: 56px;
}
.postBtn{
    background-color: #000000;
    color: #FFFFFF;
    font-size: 16px;
    font-family:'Inter', sans-serif;
    height: 40px;
    width: 130px;
}
.postLink{
    font-size: 16px;
    padding-right: 15px;
}
